/* 查询条件 */
.sx-query-bar {
  position: relative;
  margin-bottom: 8px;
  margin-left: -8px;
  margin-right: -8px;
  padding-bottom: 8px;
  background: #fff;
  border-bottom: 8px solid #f0f2f5;
}
.sx-query-bar.with-collapse {
  padding-right: 55px;
}
.sx-query-bar .sx-query-bar-collapsed {
  position: absolute;
  right: 0;
  top: 0;
  padding: 3px 8px 0 0;
  font-size: 12px;
}
.sx-query-bar .sx-query-bar-collapsed i {
  margin-left: 3px;
}
.sx-query-bar .ant-form-item {
  margin-bottom: 0;
}
